import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Intro from './intro';
import Awards from './awards';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAlignJustify } from '@fortawesome/free-solid-svg-icons';
import { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

const routes = [
  {
    path: "/intro",
    exact: true,
    sidebar: () => <div>Intro</div>,
    main: () => <Intro/>
  },
  {
    path: "/awards",
    sidebar: () => <div>Awards!</div>,
    main: () => <Awards/>
  }
];

class Profile extends Component {

    render() {
        return (
            <Router>
            <div>
            <div className="wrapper">
                 <nav id="sidebar">
                    <div className="sidebar-header">
                        <h3>Satish Singaram</h3>
                    </div>
                    <ul className="list-unstyled components">
                        <li>
                            <Link to="/awards">Awards</Link>
                        </li>
                        <li>
                            <Link to="/Intro">Intro</Link>
                        </li>
                    </ul>
                 </nav>

                <div id="content">
                <nav className="navbar navbar-expand-lg navbar-light bg-light collapse" id="iconBar">
                   <div className="container-fluid">
                      <UncontrolledDropdown>
                        <DropdownToggle caret>
                          <FontAwesomeIcon icon={faAlignJustify} fixedWidth style={{ color: 'gold' }}/>
                        </DropdownToggle>
                        <DropdownMenu>
                          <DropdownItem header><Link to="/awards">Awards</Link></DropdownItem>
                          <DropdownItem header><Link to="/Intro">Intro</Link></DropdownItem>
                        </DropdownMenu>
                      </UncontrolledDropdown>
                   </div>
                </nav>


                  {routes.map((route, index) => (
                                // You can render a <Route> in as many places
                                // as you want in your app. It will render along
                                // with any other <Route>s that also match the URL.
                                // So, a sidebar or breadcrumbs or anything else
                                // that requires you to render multiple things
                                // in multiple places at the same URL is nothing
                                // more than multiple <Route>s.
                                <Route
                                  key={index}
                                  path={route.path}
                                  exact={route.exact}
                                  component={route.main}
                                />
                              )
                             )
                  }

                </div>
            </div>
            </div>
            </Router>
        );
    }
}

export default Profile;